import React, { ReactNode } from 'react';
import { FieldError, FormFieldContainer } from "./Style";

interface IAuthTextFieldProps {
    icon: ReactNode;
    placeHolder: string;
    err: string | null;
    value: string;
    setValue: (value: string) => void;
    type?: string;
    fieldId: string;
    onFieldChange: () => void;
}

const AuthTextField: React.FC<IAuthTextFieldProps> = ({ fieldId, icon, placeHolder, err, value, setValue, type, onFieldChange }) => {
    return (
        <>
            <FormFieldContainer
                className={`authTextInputContainer ${err ? 'hasError' : ''}`}
                onClick={e => {

                }}
            >
                <span className='fieldIconFrame'>{icon}</span>
                <input
                    id={fieldId}
                    type={type ? type : 'text'}
                    placeholder={placeHolder}
                    value={value}
                    className={'authTextInput'}
                    onChange={e => {
                        setValue(e.target.value)

                        onFieldChange();
                    }}
                />
            </FormFieldContainer>
            {
                err

                &&

                <FieldError>{err}</FieldError>
            }
        </>
    );
}

export default AuthTextField;