import { createAsyncThunk, createSlice, PayloadAction } from "@reduxjs/toolkit";
import axios from 'axios';

export interface IAPIFieldError {
    location: string;
    msg: string;
    path: string;
    type: string;
    value: string;
}

export interface IFieldError {
    field: string,
    msg: string
}

//helpers
export const getFieldErrors = (errors: IAPIFieldError[]) => {

    let fieldErrors: IFieldError[] = [];
    let n = 0;

    for (const error of errors) {
        fieldErrors[n++] = { field: error.path, msg: error.msg };
    }

    return fieldErrors;
}

class LoginErrs {
    form: string | null = null;
    email: string | null = null;
    password: string | null = null;
}

interface IUser {
    authToken: string;
    id: string;
    email: string;
    role: string;
}

interface ILoginArg {
    email: string;
    password: string;
}

export interface IAuthState {
    user: IUser | null;
    loggingIn: boolean;
    loginErrs: LoginErrs;
}

const initialState = {
    user: localStorage?.user ? JSON.parse(localStorage.user) : null,
    loggingIn: false,
    loginErrs: {
        form: null,
        email: null,
        password: null
    }
} as IAuthState;

const authSlice = createSlice({
    name: 'auth',
    initialState,
    reducers: {
        resetLoginForm: (state: IAuthState) => {
            state.loginErrs = new LoginErrs();
        },
        clearLoginFieldErr: (state: IAuthState, action: PayloadAction<string>) => {
            const field = action.payload;

            for (const key in state.loginErrs) {
                if (field === key) {
                    state.loginErrs[key as keyof LoginErrs] = null;
                    break;
                }
            }

        },
        tryClearLoginFormErr: (state: IAuthState) => {
            let shouldClear = true;

            for (const key in state.loginErrs) {
                if (key === 'form') {
                    continue;
                }

                if (state.loginErrs[key as keyof LoginErrs]) {
                    shouldClear = false;
                    break;
                }

            }

            if (shouldClear) {
                state.loginErrs.form = null;
            }
        },
        loginStart: (state: IAuthState) => {
            state.loginErrs = new LoginErrs();
            state.loggingIn = true;
        },
        loginSuccess: (state: IAuthState, action: PayloadAction<IUser>) => {

            state.loggingIn = false;
            state.user = action.payload;

            localStorage.user = JSON.stringify(state.user);

        },
        loginFail: (state: IAuthState, action: PayloadAction<IFieldError[]>) => {

            for (const err of action.payload) {
                for (const key in state.loginErrs) {
                    if (err.field === key) {
                        state.loginErrs[key as keyof LoginErrs] = err.msg;
                        break;
                    }
                }
            }

            state.loggingIn = false;

        }
    }
});

export const {
    resetLoginForm,
    clearLoginFieldErr,
    tryClearLoginFormErr,
    loginStart,
    loginSuccess,
    loginFail
} = authSlice.actions;

export const tryLogin = createAsyncThunk('auth/tryLogin', async (arg: ILoginArg, { dispatch }) => {

    const { email, password } = arg;

    const endpoint = `${process.env.REACT_APP_SERVER_ENDPOINT}/api/login`;

    dispatch(loginStart());

    axios.post(endpoint, { email, password }, {
        headers: {
            "Content-Type": "application/json"
        }
    }).then(response => {
        dispatch(loginSuccess(response.data));
    }).catch(err => {
        let errs: IFieldError[] = [];

        const data = err.response.data;

        if (data) {
            if (data.errors) {
                errs = getFieldErrors(data.errors);
            } else if (data.err) {
                errs = [
                    {
                        field: 'form',
                        msg: data.err
                    }
                ]
            } else {
                errs = [
                    {
                        field: 'form',
                        msg: 'Check your internet connection.'
                    }
                ]
            }
        } else {
            throw new Error('in tryLogin: err-response-data missing');
        }

        dispatch(loginFail(errs));

    });
})

export default authSlice.reducer;