import { useSelector } from "react-redux";
import { punctuateNumber } from "../../helpers";
import { ICustomerState } from "../../stateManagement/reducers/customerReducer";
import { IRootState } from "../../stateManagement/store";
import { AccDetailsContainer } from "./Style";

const AccountDetails = () => {

    const customer = useSelector<IRootState, ICustomerState>(state => state.customer);

    return (
        <>
            {
                (customer && customer.data)

                &&
                <AccDetailsContainer>
                    <div className="accTypeNumber">
                        <h1 className="accType">{`J CREA8-E-${customer.data.bankAcc.accType.toUpperCase()}`}</h1>
                        <p className="accNumber">{customer.data.bankAcc.accNumber}</p>
                    </div>
                    <div className="accBalance">
                        <p className="balanceHeader">Account Balance</p>
                        <h1 className="balanceValue">{`$ ${punctuateNumber(customer.data.bankAcc.accBalance)}.00`}</h1>
                    </div>
                </AccDetailsContainer>
            }
        </>

    );
}

export default AccountDetails;