import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import AccNameNumber from "../../components/AccNameNumber/AccNameNumber";
import Btn from "../../components/Btn/Btn";
import { FormFieldContainer } from "../../components/FormFields/Style";
import TextField from "../../components/FormFields/TextField";
import { CUSTOMER } from "../../config/constants";
import { ICustomerState } from "../../stateManagement/reducers/customerReducer";
import { selectNav } from "../../stateManagement/reducers/navBarsReducer";
import { IRootState } from "../../stateManagement/store";
import { TransferContainer } from "./Style";
import { ReactComponent as OptionForwardIcon } from '../../assets/svgs/optionForward.svg';
import { punctuateNumber } from "../../helpers";
import { ITransactionsState, resetGetBeneficiaryErrs, tryGetBeneficiary } from "../../stateManagement/reducers/transactionsReducer";
import { IAuthState } from "../../stateManagement/reducers/authReducer";


const Transfer = () => {

    const dispatch = useDispatch<any>();

    const { user } = useSelector<IRootState, IAuthState>(state => state.auth);
    const customer = useSelector<IRootState, ICustomerState>(state => state.customer);
    // const beneficiary = useSelector<IRootState, IBeneficiary | null>(state => state.transactions.beneficiary);
    const { beneficiary, getBeneficiaryErrs } = useSelector<IRootState, ITransactionsState>(state => state.transactions);


    const [headers, setHeaders] = useState<string[]>([]);

    const [transferPg, setTransferPg] = useState('');

    const [accNumber, setAccNumber] = useState('');
    const [amount, setAmount] = useState('0');
    const [remarks, setRemarks] = useState('');
    const [pin, setPin] = useState('');

    const ACCOUNT = 'account';
    const BANKS = 'banks';
    const DETAILS = 'details';
    const REVIEW = 'reviews';


    const onFieldChange = (field: string) => {

    }

    const [present, setPresent] = useState(ACCOUNT);


    const Account = () => {

        const [value, setValue] = useState(accNumber);
        const [going, setGoing] = useState(true);
        const onContinue = () => {
            setGoing(true);
            setPresent(BANKS);
        }

        const onBlur = () => {
            setAccNumber(value);
            if (value !== '') {
                onContinue();
            }
        }


        const thisFieldChange = () => {
            onFieldChange('accNumber');
        }

        useEffect(() => {

            if (transferPg !== 'account') {


                setTransferPg('account');
                setHeaders([
                    '',
                    'Account',
                    'Banks'
                ]);
            }

        }, [transferPg]);

        useEffect(() => {
            if (value === '') {
                setGoing(true);
            } else {
                setGoing(false);
            }
        }, [value])

        return (
            <>
                <div className="formFields" >
                    <p className="newBeneficiary">NEW BENEFICIARY:</p>
                    <TextField
                        placeHolder="Type Account Number"
                        icon={null}
                        err={null}
                        value={value}
                        setValue={setValue}
                        type='number'
                        fieldId="accNumberField"
                        onFieldChange={thisFieldChange}
                        onBlur={() => onBlur()}
                    />
                </div>
                <Btn
                    text="Continue"
                    className="continueBtn"
                    onClick={() => onContinue()}
                    isActive={going}
                />
            </>
        );
    }

    const Banks = () => {

        const [lBankName, setLBankName] = useState('Chase');

        const [first, setFirst] = useState(true);
        const [gone, setGone] = useState(false);

        const selectBank = () => {
            if (lBankName !== '' && accNumber !== '') {
                dispatch(tryGetBeneficiary({
                    authToken: user?.authToken ?? '',
                    bankName: lBankName,
                    accNumber
                }));
            }
        }

        useEffect(() => {


            if (transferPg !== 'banks') {

                setTransferPg('banks');

                setHeaders([
                    'Account',
                    'Banks',
                    'Details'
                ]);

            }

        }, [transferPg]);

        useEffect(() => {

            if (getBeneficiaryErrs.accNumber || getBeneficiaryErrs.bankName) {
                alert('The intended beneficiary\'s account is not trusted. Try another beneficiary.');
                dispatch(resetGetBeneficiaryErrs());
                setPresent(ACCOUNT);
            } else if (getBeneficiaryErrs.form) {
                alert(`Error: ${getBeneficiaryErrs.form}`);
                dispatch(resetGetBeneficiaryErrs());
                setPresent(ACCOUNT);
            }

        }, [getBeneficiaryErrs.form]);

        useEffect(() => {
            if (beneficiary) {
                setPresent(DETAILS);
            }
        }, [beneficiary]);

        return (
            <div onClick={() => selectBank()} >
                SelectBank
            </div>
        );
    }

    const Details = () => {

        const [lAmount, setLAmount] = useState(amount);
        const [lRemarks, setLRemarks] = useState(remarks);
        const [lCustomer, setLCustomer] = useState('');

        const onBlur = () => {
        }

        const thisFieldChange = (field: string) => {

        }

        const [going, setGoing] = useState(false);

        const onContinue = () => {
            setAmount(lAmount);
            setRemarks(lRemarks);
            setGoing(true);
            setPresent(REVIEW);
        }

        useEffect(() => {

            if (transferPg !== 'details') {

                setTransferPg('details');

                setHeaders([
                    'Banks',
                    'Details',
                    'Review'
                ]);

            }

        }, [transferPg]);

        useEffect(() => {

            if (customer.data && lCustomer === '') {
                setLCustomer(`${customer.data.firstName.toUpperCase()} ${customer.data.middleName.toUpperCase()} ${customer.data.lastName.toUpperCase()}`)
            }

        }, [customer.data]);

        useEffect(() => {
            if (lAmount === '0') {
                setGoing(true);
            } else {
                setGoing(false);
            }
        }, [lAmount]);

        return (
            <>
                <div className="formFields" >
                    <p className="newBeneficiary">ENTER AN AMOUNT ($):</p>
                    <TextField
                        placeHolder="0.00"
                        icon={null}
                        err={null}
                        value={lAmount}
                        setValue={setLAmount}
                        type='cash'
                        fieldId="amountField"
                        onFieldChange={() => thisFieldChange('amount')}
                        onBlur={() => onBlur()}
                    />
                </div>
                <div className="formFields" >
                    <p className="newBeneficiary">REMARKS:</p>
                    <TextField
                        placeHolder="Remarks"
                        icon={null}
                        err={null}
                        value={lRemarks}
                        setValue={setLRemarks}
                        type='text'
                        fieldId="remarksField"
                        onFieldChange={() => thisFieldChange('remarks')}
                        onBlur={() => onBlur()}
                    />
                </div>
                {
                    customer.data

                    &&

                    <div className="formFields" >
                        <p className="newBeneficiary">CUSTOMER DETAIL:</p>
                        <TextField
                            placeHolder="cDetails"
                            icon={null}
                            err={null}
                            value={lCustomer}
                            setValue={() => { }}
                            type='text'
                            fieldId="customerField"
                            onFieldChange={() => thisFieldChange('remarks')}
                            onBlur={() => onBlur()}
                        />
                    </div>
                }
                <Btn
                    text="Continue"
                    className="continueBtn"
                    onClick={() => onContinue()}
                    isActive={going}
                />
            </>
        );
    }

    const Review = () => {

        const [lAmount, setLAmount] = useState(amount);
        const [lPin, setLPin] = useState(pin);

        const onBlur = () => {
        }

        const thisFieldChange = (field: string) => {

        }

        const [isGoing, setIsGoing] = useState(false);

        const onConfirm = () => {

            setAmount(lAmount);
            setLPin(pin);
        }

        useEffect(() => {

            if (transferPg !== 'review') {

                setTransferPg('review');

                setHeaders([
                    'Details',
                    'Review',
                    ''
                ]);

            }

        }, [transferPg]);

        return (
            <>
                {

                    customer.data

                    &&

                    <div className="formFields" >
                        <p className="newBeneficiary">FROM ACCOUNT:</p>
                        <FormFieldContainer className="option" >
                            <AccNameNumber />
                            <p className="balance" >{`$ ${punctuateNumber(customer.data.bankAcc.accBalance)}.00`}</p>
                        </FormFieldContainer>
                    </div>
                }
                {
                    beneficiary

                    &&

                    <div className="formFields" >
                        <p className="newBeneficiary">TO BENEFICIARY:</p>
                        <FormFieldContainer className="option" >
                            <AccNameNumber fullName={`${beneficiary.firstName.toUpperCase()} ${beneficiary.middleName.toUpperCase()} ${beneficiary.lastName.toUpperCase()} `} accNumber={beneficiary.accountNumber} />
                            <div className="optionIconFrame"><OptionForwardIcon /></div>
                        </FormFieldContainer>
                    </div>
                }
                <div className="formFields" >
                    <p className="newBeneficiary">AMOUNT ($):</p>
                    <TextField
                        placeHolder="0.00"
                        icon={null}
                        err={null}
                        value={lAmount}
                        setValue={setLAmount}
                        type='cash'
                        fieldId="amountField"
                        onFieldChange={() => thisFieldChange('amount')}
                        onBlur={() => onBlur()}
                    />
                </div>
                <div className="formFields" >
                    <p className="newBeneficiary">TRANSACTION PIN</p>
                    <TextField
                        placeHolder="Transaction PIN"
                        icon={null}
                        err={null}
                        value={lPin}
                        setValue={setLPin}
                        type='password'
                        fieldId="pinField"
                        onFieldChange={() => thisFieldChange('remarks')}
                        onBlur={() => onBlur()}
                    />
                </div>
                <Btn
                    text="Confirm Transfer"
                    className="continueBtn"
                    onClick={() => onConfirm()}
                    isActive={isGoing}
                />
            </>
        );
    }


    useEffect(() => {
        dispatch(selectNav({ role: CUSTOMER, navIndex: 1 }));
    }, []);

    return (
        <TransferContainer>
            {
                headers.length >= 3

                &&

                <h1 className="transferHeader">
                    <div className="headerArrowContainer">< span className="headerArrow"></span></div>
                    <span className="transferH1">{headers[0]}</span>
                    <span className="transferH2">{headers[1]}</span>
                    <span className="transferH3">{headers[2]}</span>
                </h1>
            }
            <div className="transferBody">
                {
                    present === ACCOUNT

                    &&

                    <Account />
                }
                {
                    present === BANKS

                    &&

                    <Banks />
                }
                {
                    present === DETAILS

                    &&

                    <Details />
                }
                {
                    present === REVIEW

                    &&

                    <Review />
                }
            </div>
        </TransferContainer>
    );
}

export default Transfer;