import { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { useSelector } from "react-redux";
import { CREDIT } from "../../config/constants";
import { getFirst3Letters, getFirstXLetters, getLast3Letters, punctuateNumber } from "../../helpers";
import { IAuthState } from "../../stateManagement/reducers/authReducer";
import { ITransaction, ITransactionsState, tryGetTransactions } from "../../stateManagement/reducers/transactionsReducer";
import { IRootState } from "../../stateManagement/store";
import Slider from "../Slider/Slider";
import TransactionReceipt from "../TransactionReceipt/TransactionReceipt";
import { Transaction, TransactionsContainer, TransactionsList } from "./Style";

const TransactionsHistory = () => {

    const dispatch = useDispatch<any>();
    const { user } = useSelector<IRootState, IAuthState>(state => state.auth);
    const { history, gettingTransactions, getTransactionsErr } = useSelector<IRootState, ITransactionsState>(state => state.transactions);

    const [transIsOpened, setTransIsOpened] = useState(false);
    const [openedTransaction, setOpenedTransaction] = useState<ITransaction | null>(null);

    useEffect(() => {

        dispatch(tryGetTransactions(user?.authToken ?? ''));
        console.log('history: ', history);


        if (history.length) {
        }

    }, [history.length]);

    useEffect(() => {

        if (getTransactionsErr) {
            alert(`Could not fetch transactions: ${getTransactionsErr}`);
        }

    }, [getTransactionsErr]);

    return (
        <>
            {
                history.length

                &&

                <TransactionsContainer>
                    <Slider positions={[{ name: 'History' }]} />
                    <TransactionsList>
                        {
                            history.map(transaction => {

                                const sign = transaction.type === CREDIT ? '+' : '-';

                                const peer = transaction.transactionPeer;

                                return (
                                    <Transaction
                                        key={transaction.refNumber}
                                        onClick={() => {
                                            setOpenedTransaction(transaction);
                                            setTransIsOpened(true);
                                        }}
                                    >
                                        <div className="channelAmount">
                                            <h1 className="channel" >E-CHANNELS</h1>
                                            <span className={`transactionAmount ${transaction.type}`} >
                                                {`${sign} $${punctuateNumber(transaction.amount)}`}
                                            </span>
                                        </div>
                                        <div className='brokenSentence'>
                                            <span className="sent">
                                                <span className="absoluteSent">
                                                    {`-${getFirst3Letters(peer.accountNumber)}--${getLast3Letters(peer.accountNumber)} - ${transaction.refNumber} - ${peer.firstName.toUpperCase()} ${peer.middleName.toUpperCase()} ${peer.lastName.toUpperCase()} - ${transaction.remarks}`}
                                                </span>
                                            </span>
                                            {
                                                transaction.remarks

                                                &&

                                                <span className="3Dots" >...</span>
                                            }
                                        </div>
                                    </Transaction>
                                );
                            })
                        }
                    </TransactionsList>
                    {
                        transIsOpened

                        &&

                        <TransactionReceipt
                            transaction={openedTransaction}
                            isOpen={transIsOpened}
                            setIsOpen={setTransIsOpened}
                        />
                    }
                </TransactionsContainer>
            }
        </>
    );
}

export default TransactionsHistory;