import { createSlice, createAsyncThunk, PayloadAction } from "@reduxjs/toolkit"
import axios from "axios";
import { response } from "express";


interface IFetchCustomerArg {
    authToken: string;
}

interface ICustomer {
    id: string;
    firstName: string;
    lastName: string;
    middleName: string;
    dateOfBirth: Date;
    gender: string;
    bankAcc: {
        id: string;
        accType: string;
        accNumber: string;
        accBalance: number;

    }
}

export interface ICustomerState {
    fetching: boolean;
    fetchErr: string | null;
    data: ICustomer | null;
}

const initialState = {
    fetching: false,
    fetchErr: null,
    data: null
} as ICustomerState;

const customerSlice = createSlice({
    name: 'customer',
    initialState,
    reducers: {
        resetForm: (state: ICustomerState) => {
            state.fetchErr = null;
        },
        fetchCustomerStart: (state: ICustomerState) => {
            state.fetchErr = null;
            state.fetching = true;
        },
        fetchCustomerSuccess: (state: ICustomerState, action: PayloadAction<ICustomer>) => {

            state.fetching = false;
            state.data = action.payload;
        },
        fetchCustomerFail: (state: ICustomerState, action: PayloadAction<string>) => {

            state.fetchErr = action.payload;
            state.fetching = false;
        }
    }
});

export const {
    resetForm,
    fetchCustomerStart,
    fetchCustomerSuccess,
    fetchCustomerFail
} = customerSlice.actions;

export const tryFetchCustomer = createAsyncThunk('customer/tryFetchCustomer', async (arg: IFetchCustomerArg, { dispatch }) => {

    const { authToken } = arg;

    const endpoint = `${process.env.REACT_APP_SERVER_ENDPOINT}/api/auth/customer`;

    dispatch(fetchCustomerStart());
    axios.get(endpoint, {
        headers: {
            "Content-Type": "application/json",
            "Authorization": `Bearer ${authToken}`
        }
    }).then(response => {
        dispatch(fetchCustomerSuccess(response.data));
    }).catch(err => {

        const data = err.response.data;

        let errMsg = '';

        if (data) {
            if (data.err) {
                errMsg = data.err
            } else {
                errMsg = 'Check your internet connection.'
            }
        }

        dispatch(fetchCustomerFail(errMsg));
    })
})

export default customerSlice.reducer;