import { createAsyncThunk, createSlice, PayloadAction } from "@reduxjs/toolkit";
import axios from "axios";
import { getFieldErrors, IFieldError } from "./authReducer";

export interface ITransaction {
    refNumber: string;
    bankAccId: string;
    type: string;
    amount: number;
    remarks: string;
    createdAt: Date;

    transactionPeer: {
        firstName: string;
        lastName: string;
        middleName: string;
        bankName: string;
        accountNumber: string;
    }
}

export interface IBeneficiary {
    id: string;
    firstName: string;
    lastName: string;
    middleName: string;
    bankName: string;
    accountNumber: string;
}

class BeneficiaryErrs {
    form: string | null = null;
    bankName: string | null = null;
    accNumber: string | null = null;
}

export interface ITransactionsState {
    gettingTransactions: boolean;
    getTransactionsErr: string | null;
    history: ITransaction[];
    gettingBeneficiaries: boolean;
    getBeneficiariesErr: string | null;
    beneficiaries: IBeneficiary[];
    gettingBeneficiary: boolean;
    getBeneficiaryErrs: BeneficiaryErrs;
    beneficiary: IBeneficiary | null;
}

const initialState = {
    gettingTransactions: false,
    getTransactionsErr: null,
    history: [],
    gettingBeneficiaries: false,
    getBeneficiariesErr: null,
    beneficiaries: [],
    gettingBeneficiary: false,
    getBeneficiaryErrs: {
        form: null,
        bankName: null,
        accNumber: null
    },
    beneficiary: null
} as ITransactionsState;

const transactionsSlice = createSlice({
    name: 'transactions',
    initialState,
    reducers: {
        getTransactionsStart: (state: ITransactionsState) => {
            state.getTransactionsErr = null;
            state.gettingTransactions = true;
        },
        getTransactionsSuccess: (state: ITransactionsState, action: PayloadAction<ITransaction[]>) => {
            state.history = action.payload;
            state.gettingTransactions = false;
        },
        getTransactionsFail: (state: ITransactionsState, action: PayloadAction<string>) => {
            state.getTransactionsErr = action.payload;
            state.gettingTransactions = false;
        },
        getBeneficiariesStart: (state: ITransactionsState) => {
            state.getBeneficiariesErr = null;
            state.gettingBeneficiaries = true;
        },
        getBeneficiariesSuccess: (state: ITransactionsState, action: PayloadAction<IBeneficiary[]>) => {
            state.beneficiaries = action.payload;
            state.gettingBeneficiaries = false;
        },
        getBeneficiariesFail: (state: ITransactionsState, action: PayloadAction<string>) => {
            state.getBeneficiariesErr = action.payload;
            state.gettingBeneficiaries = false;
        },
        resetGetBeneficiaryErrs: (state: ITransactionsState) => {
            state.getBeneficiaryErrs = new BeneficiaryErrs();
        },
        getBeneficiaryStart: (state: ITransactionsState) => {
            state.getBeneficiaryErrs = new BeneficiaryErrs();
            state.gettingBeneficiary = true;
        },
        getBeneficiarySuccess: (state: ITransactionsState, action: PayloadAction<IBeneficiary>) => {
            state.gettingBeneficiary = false;
            state.beneficiary = action.payload;
        },
        getBeneficiaryFail: (state: ITransactionsState, action: PayloadAction<IFieldError[]>) => {
            for (const err of action.payload) {
                for (const key in state.getBeneficiaryErrs) {
                    if (err.field === key) {
                        state.getBeneficiaryErrs[key as keyof BeneficiaryErrs] = err.msg;
                        break;
                    }
                }
            }

            if (!state.getBeneficiaryErrs.form) {
                state.getBeneficiaryErrs.form = 'Bank name or account number is the problem.'
            }

            state.gettingBeneficiary = false;

        }
    }
});

export const {
    getTransactionsStart,
    getTransactionsSuccess,
    getTransactionsFail,
    getBeneficiariesStart,
    getBeneficiariesSuccess,
    getBeneficiariesFail,
    resetGetBeneficiaryErrs,
    getBeneficiaryStart,
    getBeneficiarySuccess,
    getBeneficiaryFail
} = transactionsSlice.actions;

export const tryGetTransactions = createAsyncThunk('transactions/tryGetTransactions', async (authToken: string, { dispatch }) => {

    const endpoint = `${process.env.REACT_APP_SERVER_ENDPOINT}/api/transactions`;

    dispatch(getTransactionsStart());

    axios.get(endpoint, {
        headers: {
            "Content-Type": "application/json",
            "Authorization": `Bearer ${authToken}`
        }
    }).then(response => {
        dispatch(getTransactionsSuccess(response.data.transactions));
    }).catch(err => {

        const data = err.response.data;

        let errMsg = '';

        if (data) {
            if (data.err) {
                errMsg = data.err
            } else {
                errMsg = 'Check your internet connection.'
            }
        }

        dispatch(getTransactionsFail(errMsg));
    })

});

export const tryGetBeneficiaries = createAsyncThunk('transactions/tryGetBeneficiaries', async (authToken: string, { dispatch }) => {

    const endpoint = `${process.env.REACT_APP_SERVER_ENDPOINT}/api/beneficiaries`;

    dispatch(getBeneficiariesStart());

    axios.get(endpoint, {
        headers: {
            "Content-Type": "application/json",
            "Authorization": `Bearer ${authToken}`
        }
    }).then(response => {
        dispatch(getBeneficiariesSuccess(response.data.beneficiaries))
    }).catch(err => {


        const data = err.response.data;

        let errMsg = '';

        if (data) {
            if (data.err) {
                errMsg = data.err
            } else {
                errMsg = 'Check your internet connection.'
            }
        }

        dispatch(getBeneficiariesFail(errMsg));
    });

});

interface IGetBeneficiaryArg {
    authToken: string;
    bankName: string;
    accNumber: string;
}

export const tryGetBeneficiary = createAsyncThunk('transactions/tryGetBeneficiary', async (arg: IGetBeneficiaryArg, { dispatch }) => {
    const { accNumber, bankName, authToken } = arg;

    const endpoint = `${process.env.REACT_APP_SERVER_ENDPOINT}/api/beneficiary?bankName=${bankName}&accNumber=${accNumber}`;

    dispatch(getBeneficiaryStart());

    axios.get(endpoint, {
        headers: {
            "Content-Type": "application/json",
            "Authorization": `Bearer ${authToken}`
        }
    }).then(response => {
        dispatch(getBeneficiarySuccess(response.data));
    }).catch(err => {
        let errs: IFieldError[] = [];

        const data = err.response.data;

        if (data) {
            if (data.errors) {
                errs = getFieldErrors(data.errors);
            } else if (data.err) {
                errs = [
                    {
                        field: 'form',
                        msg: data.err
                    }
                ]
            } else {
                errs = [
                    {
                        field: 'form',
                        msg: 'Check your internet connection.'
                    }
                ]
            }
        } else {
            throw new Error('in tryLogin: err-response-data missing');
        }

        dispatch(getBeneficiaryFail(errs));
    });

});

export default transactionsSlice.reducer;