import React from 'react';
import { BtnContainer } from "./Style";

interface IBtnProps {
    text: string,
    onClick: () => void,
    className: string,
    isActive: boolean,
}


const Btn: React.FC<IBtnProps> = ({ text, onClick, className, isActive }) => {
    return (
        <BtnContainer
            className={className}
            onClick={() => {
                if (!isActive) {
                    onClick();
                }
            }}
        >
            {text}
            {

                isActive

                &&

                <div className="btnFg" ></div>
            }
        </BtnContainer>
    );
}

export default Btn;