import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { CREDIT, DEBIT } from "../../config/constants";
import { punctuateNumber } from "../../helpers";
import { ICustomerState } from "../../stateManagement/reducers/customerReducer";
import { ITransaction } from "../../stateManagement/reducers/transactionsReducer";
import { IRootState } from "../../stateManagement/store";
import LogoNTitle from "../LogoNTitle/LogoNTitle";
import { TransactionContainer, TransactionPaper } from "./Style";

interface ITransactionProps {
    transaction: ITransaction | null;
    setIsOpen: (isOpen: boolean) => void;
    isOpen: boolean;
}

interface IReceiptField {
    field: string;
    value: string;
}

const getSenderFullName = (transaction: ITransaction, customer: ICustomerState) => {
    let name = '';

    if (transaction.type === CREDIT) {      //The sender is the transaction-peer
        const peer = transaction.transactionPeer;

        name = `${peer.firstName.toUpperCase()} ${peer.middleName.toUpperCase()} ${peer.lastName.toUpperCase()}`;
    } else if (customer.data) {        //This customer is the sender
        name = `${customer.data.firstName.toUpperCase()} ${customer.data.middleName.toUpperCase()} ${customer.data.lastName.toUpperCase()}`;
    }

    return name;
}

const getReceiverFullName = (transaction: ITransaction, customer: ICustomerState) => {
    let name = '';

    if (transaction.type === DEBIT) {      //The sender is the customer
        const peer = transaction.transactionPeer;

        name = `${peer.firstName.toUpperCase()} ${peer.middleName.toUpperCase()} ${peer.lastName.toUpperCase()}`;
    } else if (customer.data) {        //This customer is the receiver
        name = `${customer.data.firstName.toUpperCase()} ${customer.data.middleName.toUpperCase()} ${customer.data.lastName.toUpperCase()}`;
    }

    return name;
}

const TransactionReceipt: React.FC<ITransactionProps> = ({ transaction, setIsOpen, isOpen }) => {

    const [fields, setFields] = useState<IReceiptField[] | null>(null);

    const customer = useSelector<IRootState, ICustomerState>(state => state.customer);

    const closeWithStyle = () => {


        //TODO: implement cleanup animation within .25s


        setTimeout(() => setIsOpen(false), 250);

    }


    useEffect(() => {
        if (!fields && transaction) {
            setFields([
                {
                    field: 'Transaction Date',
                    value: ` ${(new Date(transaction.createdAt)).toDateString()}`
                },
                {
                    field: 'Transaction Identification',
                    value: ` ${transaction.refNumber}`
                },
                {
                    field: 'Sender',
                    value: getSenderFullName(transaction, customer)
                },
                {
                    field: 'Transaction Amount',
                    value: `${punctuateNumber(transaction.amount)}USD`
                },
                {
                    field: 'Transaction Type',
                    value: ` NIP Transfer`
                },
                {
                    field: 'Receiver',
                    value: getReceiverFullName(transaction, customer)
                },
                {
                    field: 'Remarks',
                    value: transaction.remarks
                },
            ]);
        }

    }, [transaction]);

    return (
        <>
            {
                transaction && isOpen

                &&

                <TransactionContainer>
                    <div
                        className="transactionBg"
                        onClick={closeWithStyle}
                    ></div>
                    <TransactionPaper>
                        <LogoNTitle className="receiptLogoNTitle" />
                        <h1 className="receiptHeader" >Transaction Receipt</h1>
                        <ul className="receiptFields" >
                            {
                                fields?.map(field => {
                                    return (
                                        <li className="receiptField" >
                                            <span className="fieldName">{field.field}:</span>
                                            <span className="fieldValue">{field.value}</span>
                                        </li>
                                    );
                                })
                            }
                        </ul>
                        {
                            transaction.type === DEBIT

                            &&

                            <div className="disclaimer">
                                <h2>DISCLAIMER</h2>
                                <p>Your transfer has been successful and the beneficiary's account will be credited. However, this does not serve as confirmation of credit into the beneficiary's account. Due to the nature of the Internet, transactions may be subject interruption, transmission blackout, delayed transmission and incorrect transmission. The bank is not liable for  malfunctions in  communications facilities not within its control that may affect the accuracy or timelines of messages and transactions you send. All transactions are subject to verification and our normal fraud checks.</p>
                            </div>
                        }
                    </TransactionPaper>
                </TransactionContainer>
            }
        </>
    );
}

export default TransactionReceipt;