//Naviagation Reducer

import { ReactNode } from "react";
import { CUSTOMER } from "../../config/constants";
import { ReactComponent as BankIcon } from '../../assets/svgs/bank.svg';
import { ReactComponent as TransferIcon } from '../../assets/svgs/transfer.svg';
import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface INav {
    navIndex: number;
    name: string;
    icon: ReactNode;
    bankRoute: string;
    selected: boolean;
}

export interface INavBarsState {
    [CUSTOMER]: {
        navs: INav[];
    }
}


interface ISelectNavArg {
    role: string;
    navIndex: number;
}

const initialState = {
    [CUSTOMER]: {
        navs: [
            {
                navIndex: 0,
                name: 'Account',
                icon: <BankIcon />,
                bankRoute: '/',
                selected: false
            },
            {
                navIndex: 1,
                name: 'Transfer',
                icon: <TransferIcon />,
                bankRoute: '/transfer',
                selected: false
            }
        ]
    }
} as INavBarsState


const navBarsSlice = createSlice({
    name: 'navBars',
    initialState,
    reducers: {
        selectNav: (state: INavBarsState, action: PayloadAction<ISelectNavArg>) => {
            const { role, navIndex } = action.payload;

            if (state[role as keyof INavBarsState]) {
                for (let i = 0; i < state[role as keyof INavBarsState].navs.length; i++) {
                    if (state[role as keyof INavBarsState].navs[i].selected && i !== navIndex) {
                        state[role as keyof INavBarsState].navs[i].selected = false;
                    } else if (i === navIndex) {
                        state[role as keyof INavBarsState].navs[i].selected = true;
                    }
                }
            }
        }
    }
});

export const {
    selectNav
} = navBarsSlice.actions;

export default navBarsSlice.reducer;
