import React, { ReactNode, useEffect, useState } from 'react';
import { FieldError, FormFieldContainer } from "./Style";

interface ITextFieldProps {
    icon: ReactNode | null;
    placeHolder: string;
    err: string | null;
    value: string;
    setValue: (value: string) => void;
    type?: string;
    fieldId: string;
    onFieldChange: () => void;
    onBlur?: () => void;
}

const TextField: React.FC<ITextFieldProps> = ({ fieldId, icon, placeHolder, err, value, setValue, type, onFieldChange, onBlur }) => {

    const [decimal, setDecimal] = useState('00');

    return (
        <>
            <FormFieldContainer
                className={`textInputContainer ${placeHolder === '0.00' ? 'rtl' : ''}  ${err ? 'hasError' : ''}`}
                onClick={e => {

                }}
            >
                {
                    icon

                    &&

                    <span className='fieldIconFrame'>{icon}</span>
                }
                <input
                    id={fieldId}
                    className='textInput'
                    type={type ? (type === 'cash' ? 'number' : type) : 'text'}
                    placeholder={placeHolder}
                    value={type === 'cash' ? `${value}.${decimal}` : value}
                    onChange={e => {

                        if (type === 'cash') {

                            if (e.target.value.length < (new String(`${value}.${decimal}`)).length) {       //back-space has been typed

                                const digit = value[value.length - 1];

                                let val = '';
                                if (value.length === 1) {
                                    setValue('0');
                                } else {
                                    for (let i = 0; i < value.length - 1; i++) {
                                        val += value[i];
                                    }
                                    setValue(val);
                                }
                                setDecimal(`${digit}${decimal[0]}`);
                                return;
                            }

                            const digit = e.target.value[e.target.value.length - 1];

                            if (decimal === '00') {
                                if (digit === '0' && value !== '0') {
                                    setValue(`${value}0`);
                                } else {
                                    setDecimal(`0${digit}`);
                                }
                            } else if (decimal[0] === '0') {
                                setDecimal(`${decimal[1]}${digit}`);
                            } else if (value === '0') {
                                setValue(`${decimal[0]}`);
                                setDecimal(`${decimal[1]}${digit}`);
                            } else {
                                setValue(`${value}${decimal[0]}`);
                                setDecimal(`${decimal[1]}${digit}`);
                            }

                        } else if (type === 'number') {
                            if (e.target.value !== 'e' && e.target.value !== '-' && e.target.value !== '+') {
                                setValue(e.target.value)
                            }
                        } else {
                            setValue(e.target.value)
                        }
                        onFieldChange();

                    }}
                    onBlur={() => {
                        if (onBlur) {
                            onBlur();
                        }
                    }}
                />
            </FormFieldContainer>
            {
                err

                &&

                <FieldError>{err}</FieldError>
            }
        </>
    );
}

export default TextField;