import { useEffect, useState } from 'react';
import { AuthForm } from "./Style";
import TextField from "../../components/FormFields/TextField";
import { ReactComponent as UserIcon } from '../../assets/svgs/person.svg';
import { ReactComponent as LockIcon } from '../../assets/svgs/lock-closed.svg';
import Btn from '../../components/Btn/Btn';
import { useSelector } from 'react-redux';
import { IRootState } from '../../stateManagement/store';
import { clearLoginFieldErr, IAuthState, tryLogin } from '../../stateManagement/reducers/authReducer';
import { useDispatch } from 'react-redux';
import AuthTextField from '../../components/FormFields/AuthTextField';

const Login = () => {

    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');

    const onFieldChange = (field: string) => {

        dispatch(clearLoginFieldErr(field));
    }


    const dispatch = useDispatch<any>();
    const { loggingIn, loginErrs } = useSelector<IRootState, IAuthState>(state => state.auth);

    const err = null;

    const loginTxt = loggingIn ? 'Logging in...' : 'Login';


    const login = () => {



        dispatch(tryLogin({ email, password }));
    }

    useEffect(() => {
        if (loginErrs.form) {
            alert(`Login Error: ${loginErrs.form}`);
        }
    }, [loginErrs.form]);



    return (
        <AuthForm>
            <AuthTextField
                fieldId='emailField'
                placeHolder='Email'
                value={email}
                setValue={setEmail}
                icon={<UserIcon />}
                err={loginErrs.email}
                onFieldChange={() => onFieldChange('email')}
            />
            <AuthTextField
                fieldId='passwordField'
                type='password'
                placeHolder='Password'
                value={password}
                setValue={setPassword}
                icon={<LockIcon />}
                err={loginErrs.password}
                onFieldChange={() => onFieldChange('password')}

            />
            <Btn
                className='loginBtn'
                text={loginTxt}
                onClick={login}
                isActive={loggingIn}
            />
            <p className='forgetPass' >Forgot password? visit our closest branch.</p>
        </AuthForm>
    );
}

export default Login;