export function punctuateNumber(number: number) {
    //convert the number to a string
    let str = number.toString();
    let newStr = "";

    let itrCount = 0; //iteration-count
    for (let index = str.length - 1; index >= 0; index--) {
        newStr +=
            ++itrCount % 3 === 0 && index !== 0 ? `${str[index]},` : `${str[index]}`;
    }

    str = ""; //wipe the string
    //reverse the new string
    for (let index = newStr.length - 1; index >= 0; index--) {
        str += newStr[index];
    }

    return str;
}


export const getFirst3Letters = (word: string) => {
    let _word = '';

    if (word.length >= 3) {
        for (let i = 0; i < 3; i++) {
            _word += word[i];
        }

    }

    return _word;
}

export const getLast3Letters = (word: string) => {
    let _word = '';

    if (word.length >= 3) {
        for (let i = word.length - 4; i < word.length; i++) {
            _word += word[i];
        }

    }

    return _word;
}

export const getFirstXLetters = (word: string, x: number) => {
    let _word = '';

    if (word.length >= x) {
        for (let i = 0; i < x; i++) {
            _word += word[i];
        }

    } else {
        return word;
    }

    return _word;
}