import { configureStore } from "@reduxjs/toolkit";
import { IAuthState } from "./reducers/authReducer";
import authReducer from "./reducers/authReducer";
import customerReducer, { ICustomerState } from "./reducers/customerReducer";
import navBarsReducer, { INavBarsState } from "./reducers/navBarsReducer";
import transactionsReducer, { ITransactionsState } from "./reducers/transactionsReducer";

export interface IRootState {
    auth: IAuthState;
    customer: ICustomerState;
    navBars: INavBarsState;
    transactions: ITransactionsState;
}


const store = configureStore({
    reducer: {
        auth: authReducer,
        customer: customerReducer,
        navBars: navBarsReducer,
        transactions: transactionsReducer
    },
    middleware: getDefaultMiddleware => getDefaultMiddleware({
        serializableCheck: false
    }),
});

export default store;