import React, { ReactNode, Suspense, lazy, useEffect } from 'react';
import { useSelector } from 'react-redux';
import { Routes, Route, useNavigate } from 'react-router-dom';
import Loading from './components/Loading/Loadin';
import { CUSTOMER } from './config/constants';
import Login from './pages/auth/Login';
import HOHome from './pages/Home/HOHome';
import Transfer from './pages/Transfer/Transfer';
import { IAuthState } from './stateManagement/reducers/authReducer';
import { IRootState } from './stateManagement/store';

interface IRouteGuardProps {
    conditions: boolean[];
    fallbackRoute: string;
}

const RouteGuard: React.FC<IRouteGuardProps> = ({ conditions, fallbackRoute }) => {


    const navigate = useNavigate();

    useEffect(() => {

        for (const truth of conditions) {
            if (!truth) {
                navigate(fallbackRoute, { replace: true });
                break;
            }
        }

    }, [conditions, fallbackRoute]);


    return <></>;
}

interface IAuthRouteProps {
    page: string;
}

const AuthRoute: React.FC<IAuthRouteProps> = ({ page }) => {

    let pageComponent = null;
    let RouteGuards: React.FC | null = null;

    const LAuthLayout = lazy(() => import('./layouts/AuthLayout/AuthLayout'));

    const { user } = useSelector<IRootState, IAuthState>(state => state.auth);
    const authenticated = user ? true : false;

    switch (page) {
        case '/login':
            pageComponent = <Login />;
            RouteGuards = () => {
                return (
                    <>
                    </>
                );
            }
            break;
        default:
            break;
    }

    return (
        <>
            {
                RouteGuards

                &&

                <RouteGuards />
            }
            <RouteGuard conditions={[!authenticated]} fallbackRoute="/" />
            <Suspense fallback={<Loading />}>
                <LAuthLayout children={pageComponent} />
            </Suspense>
        </>
    );
}

interface IDashboardRouteProps {
    page: string;
}


const DashboardRoute: React.FC<IDashboardRouteProps> = ({ page }) => {

    let pageComponent = null;
    let RouteGuards: React.FC | null = null;

    const LDasboardLayout = lazy(() => import('./layouts/DashboardLayout/HODashboardLayout'));

    const { user } = useSelector<IRootState, IAuthState>(state => state.auth);
    const authenticated = user ? true : false;
    const isCustomer = user?.role === CUSTOMER;


    switch (page) {
        case '/home':
            pageComponent = <HOHome />;
            RouteGuards = () => {
                return (
                    <>
                    </>
                );
            }
            break;
        case '/transfer':
            pageComponent = <Transfer />
            RouteGuards = () => {
                return (
                    <>
                        <RouteGuard conditions={[isCustomer]} fallbackRoute='/' />
                    </>
                );
            }
            break;
        default:
            break;
    }

    return (
        <>
            {
                RouteGuards

                &&

                <RouteGuards />
            }
            <RouteGuard conditions={[authenticated]} fallbackRoute='/login' />
            <Suspense fallback={<Loading />}>
                <LDasboardLayout children={pageComponent} />
            </Suspense>
        </>
    );
}

export const BankRoutes = () => {
    return (
        <Routes>
            {/* Auth routes */}
            <Route path='/login' element={<AuthRoute page='/login' />} />

            {/* Dasboard routes */}
            <Route path='/' element={<DashboardRoute page='/home' />} />
            <Route path='/transfer' element={<DashboardRoute page='/transfer' />} />
        </Routes>
    );
}