import { Provider } from "react-redux";
import { BrowserRouter as Router } from "react-router-dom";
import { BankRoutes } from "./routes";
import store from "./stateManagement/store";

function App() {

  return (
    <Provider store={store}>
      <Router>
        <div className="App">
          <BankRoutes />
        </div>
      </Router>
    </Provider>
  );
}

export default App;
