import { useEffect } from "react";
import { useDispatch } from "react-redux";
import AccountDetails from "../../components/AccDetails/AccDetails";
import TransactionsHistory from "../../components/TransactionsHistory/TransactionsHistory";
import { CUSTOMER } from "../../config/constants";
import { selectNav } from "../../stateManagement/reducers/navBarsReducer";
import { HomeContainer } from "./Style";

const CustomerHome = () => {

    const dispatch = useDispatch<any>();

    useEffect(() => {
        dispatch(selectNav({ role: CUSTOMER, navIndex: 0 }));
    }, [])

    return (
        <HomeContainer>
            <AccountDetails />
            <TransactionsHistory />
        </HomeContainer>
    );
}

export default CustomerHome;