import React from "react";
import { useSelector } from "react-redux";
import { ICustomerState } from "../../stateManagement/reducers/customerReducer";
import { IRootState } from "../../stateManagement/store";
import { AccNameNumberContainer } from "./Style";

interface IAccNameNumberProps {
    fullName?: string;
    accNumber?: string;
}

const AccNameNumber: React.FC<IAccNameNumberProps> = ({ fullName, accNumber }) => {

    const customer = useSelector<IRootState, ICustomerState>(state => state.customer);

    const name = fullName ? fullName : (customer.data ? `J CREA8-E-${customer.data.bankAcc.accType.toUpperCase()}` : null);
    const number = accNumber ? accNumber : (customer.data ? customer.data.bankAcc.accNumber : null);


    return (
        <>
            {
                (name && number)

                &&

                <AccNameNumberContainer>
                    <h1 className="accName">{name}</h1>
                    <p className="accNumber">{number}</p>
                </AccNameNumberContainer>
            }
        </>
    );
}

export default AccNameNumber;